const Search = props => (
    <div>
        <input type="text" className="searchBar" placeholder="Search"></input>
          
  
      <style jsx>{`
        .searchBar{
          background-color: white;
          height: 20px;
          margin-left: 2px;
          // padding-left: 5px;
          width: calc( 100% - 4px);
          text-align:center;

          // border: 2px solid red;

          border-radius: 100px;
          
        }
      `}
      </style>
    </div>
  );
  
  export default Search;